.app-footer {
  backdrop-filter: blur(25px);
  color: #fff;
  z-index: 10;
  background: linear-gradient(135deg, #0f172afa 0%, #1e293bf2 100%);
  border-top: 1px solid #ffffff26;
  width: 100%;
  padding: 50px 0 25px;
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  box-shadow: 0 -4px 20px #0000001a;
}

.footer-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
}

.footer-main {
  grid-template-columns: 2fr 1fr;
  align-items: start;
  gap: 60px;
  margin-bottom: 50px;
  display: grid;
}

.footer-section {
  flex-direction: column;
  display: flex;
}

.footer-title {
  color: #fff;
  letter-spacing: .6px;
  -webkit-text-fill-color: transparent;
  background: linear-gradient(135deg, #fff 0%, #e2e8f0 100%);
  -webkit-background-clip: text;
  background-clip: text;
  margin-bottom: 18px;
  font-size: 1.6rem;
  font-weight: 800;
}

.footer-description {
  color: #cbd5e1;
  opacity: .9;
  letter-spacing: .2px;
  margin-bottom: 0;
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.7;
}

.footer-subtitle {
  color: #fff;
  letter-spacing: .3px;
  margin-bottom: 16px;
  font-size: 1.1rem;
  font-weight: 600;
}

.language-selector {
  display: inline-block;
  position: relative;
}

.language-btn {
  color: #fff;
  cursor: pointer;
  backdrop-filter: blur(15px);
  background: linear-gradient(135deg, #ffffff1f 0%, #ffffff14 100%);
  border: 1px solid #ffffff40;
  border-radius: 14px;
  align-items: center;
  gap: 10px;
  padding: 14px 22px;
  font-size: .95rem;
  font-weight: 600;
  transition: all .3s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  box-shadow: 0 2px 8px #0000001a;
}

.language-btn:hover {
  background: linear-gradient(135deg, #ffffff2e 0%, #ffffff1f 100%);
  border-color: #ffffff59;
  transform: translateY(-3px);
  box-shadow: 0 4px 16px #00000026;
}

.language-btn.current {
  background: #3b82f633;
  border-color: #3b82f666;
}

.language-dropdown {
  backdrop-filter: blur(20px);
  z-index: 1000;
  opacity: 0;
  visibility: hidden;
  background: #0f172afa;
  border: 1px solid #fff3;
  border-radius: 12px;
  max-height: 300px;
  margin-top: 8px;
  transition: all .3s;
  position: absolute;
  top: 100%;
  left: 0;
  right: 0;
  overflow-y: auto;
  transform: translateY(-10px);
  box-shadow: 0 8px 32px #0000004d;
}

.language-dropdown.show {
  opacity: 1;
  visibility: visible;
  transform: translateY(0);
}

.language-option {
  color: #fff;
  text-align: left;
  cursor: pointer;
  background: none;
  border: none;
  border-bottom: 1px solid #ffffff1a;
  align-items: center;
  gap: 8px;
  width: 100%;
  padding: 12px 20px;
  font-size: .95rem;
  font-weight: 500;
  transition: all .2s;
  display: flex;
}

.language-option:last-child {
  border-bottom: none;
}

.language-option:hover {
  color: #3b82f6;
  background: #ffffff1a;
}

.footer-bottom {
  text-align: center;
  border-top: 1px solid #ffffff1a;
  justify-content: center;
  align-items: center;
  padding-top: 30px;
  display: flex;
}

.footer-info {
  flex-direction: column;
  gap: 8px;
  display: flex;
}

.footer-text {
  color: #cbd5e1;
  opacity: .8;
  font-size: .9rem;
}

.footer-version {
  color: #94a3b8;
  opacity: .6;
  font-size: .8rem;
}

@media (width <= 768px) {
  .app-footer {
    padding: 35px 0 20px;
  }

  .footer-container {
    padding: 0 20px;
  }

  .footer-main {
    grid-template-columns: 1fr;
    gap: 35px;
    margin-bottom: 35px;
  }

  .footer-section {
    text-align: center;
  }

  .footer-title {
    margin-bottom: 15px;
    font-size: 1.4rem;
  }

  .footer-description {
    font-size: 1rem;
    line-height: 1.7;
  }

  .footer-subtitle {
    margin-bottom: 15px;
    font-size: 1.1rem;
  }

  .language-selector {
    justify-content: center;
    display: flex;
  }

  .language-btn {
    border-radius: 16px;
    padding: 12px 20px;
    font-size: .95rem;
  }

  .language-dropdown {
    border-radius: 16px;
    min-width: 220px;
    left: 50%;
    transform: translateX(-50%)translateY(-10px);
  }

  .language-dropdown.show {
    transform: translateX(-50%)translateY(0);
  }

  .footer-bottom {
    text-align: center;
    flex-direction: column;
    gap: 20px;
  }

  .footer-info {
    order: 1;
  }
}

@media (width <= 480px) {
  .app-footer {
    padding: 30px 0 20px;
  }

  .footer-container {
    padding: 0 16px;
  }

  .footer-main {
    gap: 30px;
    margin-bottom: 30px;
  }

  .footer-title {
    font-size: 1.3rem;
  }

  .footer-description {
    font-size: .95rem;
    line-height: 1.6;
  }

  .language-btn {
    border-radius: 18px;
    padding: 10px 18px;
    font-size: .9rem;
  }

  .language-dropdown {
    border-radius: 18px;
    min-width: 200px;
  }

  .footer-bottom {
    gap: 20px;
  }

  .footer-text {
    font-size: .9rem;
  }

  .footer-version {
    font-size: .8rem;
  }
}

@media (width >= 769px) and (width <= 1024px) {
  .footer-main {
    grid-template-columns: 1.5fr 1fr;
    gap: 40px;
  }

  .footer-container {
    padding: 0 24px;
  }
}

@media (prefers-color-scheme: dark) {
  .app-footer {
    background: linear-gradient(135deg, #0a0e14fa 0%, #141c24f2 100%);
  }

  .language-dropdown {
    background: #0a0e14fa;
  }
}

@media (prefers-contrast: high) {
  .app-footer {
    border-top: 2px solid #fff;
  }

  .footer-link:hover {
    text-decoration: underline;
  }

  .social-link {
    border: 2px solid #fff;
  }
}

@media (prefers-reduced-motion: reduce) {
  .language-btn, .language-option, .footer-link, .social-link {
    transition: none;
  }

  .language-dropdown {
    transition: opacity .2s;
  }
}

@media (width <= 360px) {
  .app-footer {
    margin-top: 35px;
    padding: 25px 0 15px;
  }

  .footer-container {
    padding: 0 12px;
  }

  .footer-main {
    gap: 25px;
    margin-bottom: 25px;
  }

  .footer-title {
    font-size: 1.2rem;
  }

  .footer-description {
    font-size: .9rem;
    line-height: 1.5;
  }

  .language-btn {
    padding: 8px 16px;
    font-size: .85rem;
  }
}

@media (width <= 768px) and (orientation: landscape) {
  .app-footer {
    margin-top: 30px;
    padding: 25px 0 15px;
  }

  .footer-main {
    gap: 25px;
    margin-bottom: 25px;
  }
}

.app-footer {
  flex-shrink: 0;
  margin-top: auto;
}
